import React, { useEffect, useState, useContext } from 'react';
import { List } from '@material-ui/core';
import NavMenuItem from './NavMenuItem';
import NavSection from './NavSection';
import NavCollapse from './NavCollapse';
import makeStyles from '@material-ui/core/styles/makeStyles';
import AppContext from '@jumbo/components/contextProvider/AppContextProvider/AppContext';

const OptionalPages = [
  { id: '0', path: '/dashboard' },
  { id: '1', path: '/wallboard' },
  { id: '2', path: '/reports/agentstatus' },
  { id: '3', path: '/reports/campaigns' },
  { id: '4', path: '/reports/callLog' },
  { id: '5', path: '/reports/calllistening' },
  { id: '6', path: '/reports/callmanagement' },
  { id: '7', path: '/reports/calltransferreport' },
  { id: '8', path: '/reports/internalssummary' },
  { id: '9', path: '/reports/didreport' },
  { id: '10', path: '/reports/dailysummaryreport' },
  { id: '11', path: '/reports/queueanswerreport' },
  { id: '12', path: '/reports/abandoned-queues' },
  { id: '13', path: '/reports/droppedcall' },
  { id: '14', path: '/reports/abondenedcalls' },
  { id: '15', path: '/reports/calltermination' },
  { id: '16', path: '/reports/repetetive-calls' },
  { id: '17', path: '/reports/agentcdr' },
  { id: '18', path: '/reports/servicelevel' },
  { id: '19', path: '/reports/dailylog' },
  { id: '20', path: '/reports/dailyacc' },
  { id: '21', path: '/reports/callcomparison' },
  { id: '22', path: '/reports/monthlylog' },
  { id: '23', path: '/reports/monthlyDetails' },
  { id: '24', path: '/reports/quality-report' },
  { id: '25', path: '/settings/editbreaks' },
  { id: '26', path: '/settings/departmentsettings' },
  { id: '27', path: '/settings/usersettings' },
  { id: '28', path: '/settings/rolesettings' },
  { id: '29', path: '/iys/called-number' },
  { id: '30', path: '/teams/users' },
];
const useStyles = makeStyles(theme => ({
  sideNavMenu: {
    position: 'relative',
  },
}));

const CmtVertical = ({ menuItems }) => {
  const classes = useStyles();

  const [menus, setMenus] = useState([...menuItems]);
  const [allowedUrls, setAllowedUrls] = useState(OptionalPages.map(e => e.path));
  useEffect(() => {
    let reportsArr = menuItems[1].children[1].children;
    function checkLS() {
      let storage = localStorage.getItem('favs') || '';
      if (storage !== '') {
        let favorites = storage.split(',');
        let filtered = reportsArr.filter(item => favorites.includes(item.id));
        if (filtered.length > 0) {
          let temp = [...menuItems];
          temp[0].children[2].children = filtered;
          setMenus(temp);
        }
      } else {
        let temp = [...menuItems];
        temp[0].children[2].children = [];
        setMenus(temp);
      }
    }
    checkLS();
    window.addEventListener('storage', checkLS);
    return () => {
      window.removeEventListener('storage', checkLS);
    };
  }, [menuItems]);

  const { rolePages } = useContext(AppContext);
  useEffect(() => {
    let allowedPagesArr = rolePages.split(',');
    let allowedUrlsArr = OptionalPages.filter(e => allowedPagesArr.includes(e.id)).map(item => item.path);
    setAllowedUrls(allowedUrlsArr);
  }, [rolePages]);
  return (
    <List component="nav" disablePadding className={classes.sideNavMenu}>
      {menus.map((item, index) => {
        switch (item.type) {
          case 'section':
            return <NavSection {...item} key={index} allowedUrls={allowedUrls} />;
          case 'collapse':
            return <NavCollapse {...item} key={index} />;
          case 'item':
            return <NavMenuItem {...item} key={index} />;
          default:
            return null;
        }
      })}
    </List>
  );
};

export default CmtVertical;
